import React, { Component } from 'react'
import { Check } from "react-feather"
import { Link } from "react-router-dom"
import { subRooter } from '../../../configs/vars'
import {
  CardHeader,
  CardTitle,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
	Label,
	InputGroup,
	InputGroupAddon,
	Card,
  CardBody,
} from "reactstrap";
import * as Feather from "react-feather";
import Checkbox from "../../../components/@vuexy/checkbox/CheckboxesVuexy"
import BasicDropZone from "../../../components/Wrappers/BasicDropZone"
import Breadcrumbs from "../../../components/@vuexy/breadCrumbs/BreadCrumb";
import Editor from '../../../components/Wrappers/Editor'
import Select from 'react-select'
import "../../../assets/scss/plugins/forms/react-select/_react-select.scss"

import { getInitialData as getLevels } from "../../../redux/actions/level"
import { getParentData as getCategories} from "../../../redux/actions/category"
import { getDataByType as getTopics } from "../../../redux/actions/topic"
import { addData as addLesson, updateData, getInitialData as getAllLessons,  getDatabyId as getLessonbyId } from "../../../redux/actions/lesson"
import { connect } from "react-redux";
import { PlusCircle } from "react-feather";
import { getQueryStringValue } from "../../../service/helper";


export class CreateLessonPage extends Component {
	state = {
		id: '',
		name: this.props.lesson ?  this.props.lesson.name : '',
		youtubeUrl: this.props.lesson ?  this.props.lesson.youtubeUrl : '',
		keyLearnChecked: false,
		keyWords: [], // array of string
		selectedKeyWordId: null,
		uploadedImages: [],
		uploadedVideos: [],
		selectedCategoryId: '',
		selectedTopicId: '',
		selectedLevelId: '',
		description: "", // html content
		inVaild: false,
		// edit
		currentLesson: null,
	}
	formtype = '' // new | edit

	UNSAFE_componentWillReceiveProps(nextProps) {
		if(this.state.currentLesson === null && nextProps.currentLesson && !Object.is(nextProps.currentLesson, this.state.lesson)) {
			let currentLesson = nextProps.currentLesson
			this.setState({
				id: currentLesson._id,
				name: currentLesson.name,
				youtubeUrl: currentLesson.youtubeUrl,
				// keyLearnChecked: false,
				keyWords: currentLesson.learnkeys, // array of string
				selectedCategoryId: currentLesson.topic.categoryId._id,
				selectedTopicId: currentLesson.topic._id,
				selectedLevelId: currentLesson.level._id,
				description: currentLesson.description, // html content
				currentLesson: currentLesson
			})
		}
	}

	

	componentDidMount() {
		this.props.getLevels();
		this.props.getCategories();
		this.props.getTopics('LESSON');

		this.formtype = getQueryStringValue('type');
		let id = getQueryStringValue('id');
		let currentLesson = null;

		if (id) {
			currentLesson = this.props.lessons.find((elt) => elt._id === id);
		}

		if(id) {
			this.props.getLessonbyId(id);
		}

		if(currentLesson) {
			this.setState({
				id: id,
				name: currentLesson.name,
				youtubeUrl: currentLesson.youtubeUrl,
				// keyLearnChecked: false,
				keyWords: currentLesson.learnkeys, // array of string
				selectedCategoryId: currentLesson.topic.categoryId._id,
				selectedTopicId: currentLesson.topic._id,
				selectedLevelId: currentLesson.level._id,
				description: currentLesson.description, // html content
				currentLesson: currentLesson
			})
		}
	}

	handleChangeKeyWord = (value, index) => {
		let { keyWords } = this.state;

		keyWords[index] = value;
		this.setState({
			keyWords
		})
	}
	
	deleteKeyWord = (index) => {
		let { keyWords } = this.state;
		keyWords = keyWords.filter((item, i) =>
			i !== index
		);

		this.setState({
			keyWords
		})
	}

	handleFiles = (type, files) => { // type = string
		if(type === 'image') {
			this.setState({
				uploadedImages: files
			})
		} else if(type === 'video') {
			this.setState({
				uploadedVideos: files
			})
		}
	}

	handleOnSubmit = (e) => {
		e.preventDefault();

		if (this.isValid(this.state, this.formtype)) {
			if(this.formtype !== 'edit') {
				this.props.addLesson({
					name: this.state.name,
					levelId: this.state.selectedLevelId,
					youtubeUrl: this.state.youtubeUrl,
					image: this.state.uploadedImages,
					videos: this.state.uploadedVideos,
					learnkeys: this.state.keyWords,
					description: this.state.description,
					topic: this.state.selectedTopicId,
				})
			} else {
				this.props.updateData({
					_id: this.state.id,
					name: this.state.name,
					levelId: this.state.selectedLevelId,
					youtubeUrl: this.state.youtubeUrl,
					image: this.state.uploadedImages,
					videos: this.state.uploadedVideos,
					learnkeys: this.state.keyWords,
					description: this.state.description,
					topic: this.state.selectedTopicId,
				})
			}
		} else {
			this.setState({
				inVaild: true
			})
		}
	}

	isValid = (state, type) => {
		if(type === 'edit') {
			return state.name && state.selectedCategoryId && state.selectedTopicId && state.selectedLevelId
		} else {
			return state.name && state.uploadedImages.length > 0 && state.selectedCategoryId && state.selectedTopicId && state.selectedLevelId
		}
	}

	initValues = () => {
		this.setState({
			name: '',
			youtubeUrl: '',
			keyLearnChecked: false,
			keyWords: [], // array of string
			selectedKeyWordId: null,
			uploadedImages: null,
			uploadedVideos: null,
			selectedCategoryId: '',
			selectedTopicId: '',
			selectedLevelId: '',
			description: "" // html content
		})
	}

	addKeyword = () => {
		let { keyWords } = this.state;
		keyWords.push('New key word');
		this.setState({
			keyWords
		})
	}
	
	handleEditor = (strAsHtml) => {
		this.setState({
			description: strAsHtml
		})
	}

	handleChange = (name, value) => {
		if(name !== 'selectedTopicId') {
			this.setState({
				selectedTopicId: '',
				[name]: value
			})
		} else {
			this.setState({
				[name]: value
			})
		}
	}

	renderCategory = (categories) => {
		let result = []
		for (const category of categories) {
			result.push({
				value: category._id,
				label: category.name,
				isDisabled: category.children && category.children.length > 0,
			});

			if(category.children) {
				for (const child of category.children) {
					result.push({
						value: child._id,
						label: <span style={{paddingLeft: '3px'}} className='pl-1'>{child.name}</span>,
					});
				}
			}
		}
		return result;
	}
	
	render() {
		const { 
			keyWords, 
			selectedLevelId,
			selectedCategoryId, 
			selectedTopicId,
			name,
			youtubeUrl,
			description,
			currentLesson
		} = this.state;
		const {
			levels,
			categories,
			topics,
		} = this.props

		if(keyWords.length === 0) {
			keyWords.push('')
		}

		let selectOption = this.renderCategory(categories);
		const levelsOptions = levels.map((level) => ({value: level._id, label: level.name }));
		let subjectOptions = topics.filter((topic) => {
			return (topic.levelId._id === selectedLevelId || selectedLevelId === "") && (topic.categoryId._id === selectedCategoryId ||  selectedCategoryId === "")
		}).map((elt) => ({value: elt._id, label: elt.name}));

		return (
				<React.Fragment>
					<Breadcrumbs
						breadCrumbTitle="LESSON"
						breadCrumbParent="Area"
						breadCrumbActive={`${this.formtype === 'edit' ? 'Edit Lesson' : 'New Lesson'}`}
					/>
					<div className='ml-1 mb-1'>
						<Button.Ripple outline color="primary"  onClick={() => {this.props.history.push(`${subRooter}/lesson`)}}>
							<Feather.ArrowLeft size={20} />	Back to List page
						</Button.Ripple>
					</div>
					<Card>
						<CardBody>
								<Card>
									<CardHeader>
										<CardTitle>
										{
											this.formtype === 'edit' ?"Edit Lesson" : "New Lesson"
										}
										</CardTitle>
									</CardHeader>
									<CardBody>
										<Form className='text-left' onSubmit={this.handleOnSubmit}  >
											<Row>
												<Col md='4' sm="12">
													<FormGroup>
														<Label for="topic">Select Level</Label>
														<Select
															className="React"
															classNamePrefix="select"
															name="topic"
															id="topic"
															options={levelsOptions}
															value={levelsOptions.find((elt) => elt.value === selectedLevelId)}
															onChange={(e) => this.handleChange('selectedLevelId', e.value)}
														/>
													</FormGroup>
												</Col>
												<Col md='4' sm="12">
													<FormGroup>
														<Label for="topic">Select Category</Label>
														<Select
															className="React"
															classNamePrefix="select"
															name="topic"
															id="topic"
															options={selectOption}
															value={selectOption.find((elt) => elt.value === selectedCategoryId)}
															onChange={(e) => this.handleChange('selectedCategoryId', e.value)}
														/>
													</FormGroup>
												</Col>
												<Col md='4' sm="12">
													<Row>
														<Col md='10' sm='10'>
															<FormGroup>
																<Label for="topic">Select Topic</Label>
																<Select
																	className="React"
																	classNamePrefix="select"
																	name="topic"
																	id="topic"
																	options={subjectOptions}
																	value={subjectOptions.find((elt) => elt.value === selectedTopicId) || {label: "", value: ''}}
																	onChange={(e) => this.handleChange('selectedTopicId', e.value)}
																/>
															</FormGroup>
														</Col>
														<Col md='2' sm='2' className='align-self-center'>
															<FormGroup className='m-0'>
																<Link to={`${subRooter}/topics`}>
																	<PlusCircle size={30} />
																</Link>
															</FormGroup>
														</Col>
													</Row>
												</Col>
												<Col md='12' sm="12">
													<FormGroup>
														<Label for="lesson-name">Lesson Name</Label>
														<Input
															type="text"
															name="name"
															id="lesson-name"
															placeholder="Lesson Name"
															value={name}
															invalid={name === ""}
															onChange={e => this.setState({ name: e.target.value})}
														/>
														{
															name === "" && <span className='invalid-tooltip'>please fill name</span>
														}
													</FormGroup>
												</Col>
												{/* file upload form start */}
												<Col md='12'>
													<Row className='mb-1'>
														<Col md='6'>
															{
																currentLesson && currentLesson.imgUrl ? 
																	<Row className='m-1'>
																		<Col md='12'>
																			<img className='w-100' src={currentLesson.imgUrl} alt="" />
																		</Col>
																		<Col md='12' className='text-right mt-1'>
																			<Button.Ripple
																				color="danger"
																				className="mb-1"
																				onClick={() => {
																					let temp = currentLesson
																					temp.imgUrl = ""
																					this.setState({
																						currentLesson: temp
																					})
																				}}
																			>
																				Remove
																			</Button.Ripple>
																		</Col>
																	</Row>
																	:
																	<>
																		<BasicDropZone 
																			accept="image/*" 
																			type="image"
																			placeholder="Image File" 
																			multiple={false}
																			onLoadFile={(files) => this.handleFiles('image', files)}
																			style={{background: 'linear-gradient(to right, rgb(86, 171, 47), rgb(168, 224, 99))'}}
																		/>
																		{
																			this.state.uploadedImages.length === 0 && <p className='text-danger'>import an Image</p>
																		}
																	</>
															}
														</Col>
														<Col md='6'>
															{
																currentLesson && currentLesson.videos &&  currentLesson.videos.length > 0 && currentLesson.videos[0] !== "" ? 
																<Row className='m-1'>
																	<Col md='12'>
																		<video controls
																			src={currentLesson.videos[0]}
																			width="620"
																			className="w-100"
																		>
																			Sorry, your browser doesn't support embedded videos, 
																			but don't worry, you can download it
																			and watch it with your favorite video player!
																		</video>
																	</Col>
																	<Col md='12' className='text-right'>
																			<Button.Ripple
																				color="danger"
																				className="mb-1"
																				onClick={() => {
																					let temp = currentLesson
																					temp.videos = []
																					this.setState({
																						currentLesson: temp
																					})
																				}}
																			>
																				Remove
																			</Button.Ripple>
																	</Col>
																</Row>
																:
																<BasicDropZone 
																	accept=".avi, .mp4, mpg, .ogg"
																	type="video"
																	placeholder="Video File"
																	// multiple={true}
																	onLoadFile={(files) => this.handleFiles('video', files)}
																	style={{background: 'linear-gradient(to right, rgb(86, 171, 47), rgb(168, 224, 99))'}}
																/>
															}
														</Col>
													</Row>
												</Col>

												{/* file upload form end */}
												<Col sm="12">
													<FormGroup>
														<Label for="youtube-url">Youtube URL</Label>
														<Input
															type="text"
															name="youtubeUrl"
															invalid={! /^(https?\:\/\/)?(www\.youtube\.com|youtu\.?be)\/.+$/.test(youtubeUrl) && youtubeUrl !== ''}
															id="youtube-url"
															placeholder="https://www.youtube.com/watch?v=hmx-CGLBVSQ"
															value={youtubeUrl}
															onChange={e => this.setState({youtubeUrl: e.target.value})}
														/>
														{
															! /^(https?\:\/\/)?(www\.youtube\.com|youtu\.?be)\/.+$/.test(youtubeUrl) && youtubeUrl !== '' && <span className="invalid-tooltip">
																This is invalid youtube url
															</span>
														}
													</FormGroup>
												</Col>
												<Col md='12' >
													<Card>
														<CardHeader className='font-weight-500 mb-1'>
															Description
														</CardHeader>
														<Editor html={description}  onChange={(values) => this.handleEditor(values)}/>
													</Card>
												</Col>
												{/* check */}
												<Col sm="12">
														{
															keyWords.length === 1 && keyWords[0] === ''  && <p className='text-danger'>Please add keyword at least one!</p>
														}
													<Row>
														<Col sm="3" md='2'>
															<FormGroup>
																<Checkbox
																	color="primary"
																	icon={<Check className="vx-icon" size={16} />}
																	label="Key Learn"
																	defaultChecked={true}
																/>
															</FormGroup>
														</Col>
														<Col sm="9" md='10'>
															<FormGroup>
																<InputGroup>
																	<Input 
																		value={keyWords[0]} 
																		onChange={(e) => this.handleChangeKeyWord(e.target.value, 0)}
																	/>
																	<InputGroupAddon addonType="append">
																		<Button.Ripple color="success"
																			onClick={this.addKeyword}
																		>ADD</Button.Ripple>
																	</InputGroupAddon>
																</InputGroup>
															</FormGroup>
															{
																keyWords.length	 > 1 && keyWords.map((keyword, index) => {
																	if(index === 0) { return null; }
																	return (
																		<FormGroup key={index}>
																			<InputGroup>
																				<Input value={keyword} onChange={(e) => this.handleChangeKeyWord(e.target.value, index)} />
																				<InputGroupAddon addonType="append">
																					<Button.Ripple color="danger"
																						onClick={() => this.deleteKeyWord(index)}
																					>Remove</Button.Ripple>
																				</InputGroupAddon>
																			</InputGroup>
																		</FormGroup>
																	)
																})
															}
														</Col>
													</Row>
												</Col>
												<Col sm="12">
													<FormGroup className='text-right'>
														<Button.Ripple
															color="primary"
															type="submit"
															className="mr-1 mb-1"
														>
															Submit
														</Button.Ripple>
														<Button.Ripple
															outline
															color="warning"
															type="reset"
															className="mb-1"
														>
															Reset
														</Button.Ripple>
													</FormGroup>
												</Col>
											</Row>
										</Form>
									</CardBody>
								</Card>
						</CardBody>
					</Card>
			</React.Fragment>
		)
	}
}

const mapStateToProps = (state) => {
	return {
		levels: state.level.allData,
		categories: state.category.allData,
    topics: state.topic.dataPerType,
		lessons: state.lesson.allData,
		currentLesson: state.lesson.currentLesson
	}
}

const mapDispatchToProps = {
	getLevels,
	getCategories,
	getTopics,
	addLesson,
	updateData,
	getAllLessons,
	getLessonbyId
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateLessonPage)