import React from "react"
import { Editor } from "react-draft-wysiwyg"
import { EditorState, convertToRaw, ContentState } from 'draft-js';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';
// import textToHtmlParser from 'html-react-parser';
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css"
import "../../assets/scss/plugins/extensions/editor.scss"

class EditorImage extends React.Component {
  state = { 
    editorState: null,
    content: null
  }

  constructor(props) {
    super(props);
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if(this.state.content === null && nextProps.html !== "") {
      this.htmlToState(nextProps.html)
    }
  }

  htmlToState = (html) => {
    const contentBlock = htmlToDraft(html);
    if (contentBlock) {
      const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
      const editorState = EditorState.createWithContent(contentState);
      
      this.setState({
        editorState: editorState,
        content: html
      })
    }
  }

  uploadImageCallBack = file => {
    return new Promise((resolve, reject) => {
      const xhr = new XMLHttpRequest()
      xhr.open("POST", "https://api.imgur.com/3/image")
      xhr.setRequestHeader("Authorization", "Client-ID 7e1c3e366d22aa3")
      const data = new FormData()
      data.append("image", file)
      xhr.send(data)
      xhr.addEventListener("load", () => {
        const response = JSON.parse(xhr.responseText)
        resolve(response)
      })
      xhr.addEventListener("error", () => {
        const error = JSON.parse(xhr.responseText)
        reject(error)
      })
    })
  }

  handleUpdate = (changedEditorState) => {
    this.setState({
      editorState: changedEditorState
    })
    this.props.onChange(draftToHtml(convertToRaw(changedEditorState.getCurrentContent())))
  }
  
  render() {
    const { editorState } = this.state;

    return (
      <React.Fragment>
        <Editor
          editorState={editorState}
          wrapperClassName="demo-wrapper"
          editorClassName="demo-editor"
          toolbar={{
            inline: { inDropdown: true },
            list: { inDropdown: true },
            textAlign: { inDropdown: true },
            link: { inDropdown: true },
            history: { inDropdown: true },
            image: {
              uploadCallback: this.uploadImageCallBack,
              previewImage: true,
              alt: { present: true, mandatory: true }
            }
          }}
          onEditorStateChange={this.handleUpdate}
        />
        {/* <textarea
          disabled
          value={draftToHtml(convertToRaw(editorState.getCurrentContent()))}
        /> */}
        {/* {
          textToHtmlParser(draftToHtml(convertToRaw(editorState.getCurrentContent()))) /}*
        {/* } */}
      </React.Fragment>
    )
  }
}

export default EditorImage
