import React, { useState, useEffect } from 'react'
import { useDropzone } from "react-dropzone"
import { UploadCloud } from "react-feather"
import { Button } from "reactstrap"

function BasicDropzone(props) {
	/*
		protoTypes = {
			accept,  // required // string
			placeholder // string
			type  // image, video
			multiple // bloean // 
			onLoadFile // func // required
			style // object // 

			linear-gradient(to right, rgb(86, 171, 47), rgb(168, 224, 99))
		}
	*/

	const [files, setFiles] = useState([])
	const { getRootProps, getInputProps } = useDropzone({
	  accept: props.accept,
	  multiple: props.multiple || true,
	  onDrop: acceptedFiles => {
		setFiles(
		  acceptedFiles.map(file =>{
				return Object.assign(file, {
					prevew: URL.createObjectURL(file)
				})
		  })
		)
	  }
	})
  
	const thumbs = files.map(file => (
		<div className="dz-thumb p-1" key={file.name}>
		  <div className="dz-thumb-inner overflow-hidden text-center">
				{
					props.type === 'video' && 
					<video controls
						src={file.prevew}
						poster={file.prevew}
						width="620"
						className="w-100"
					>
						Sorry, your browser doesn't support embedded videos, 
						but don't worry, you can download it
						and watch it with your favorite video player!
					</video>
				}
				{
					props.type === 'image' && 
						<img src={file.prevew} className="dz-img" alt={file.name}  height='100' />
				}
				<p className='text-white mt-1'>{file.name}</p>
				
		  </div>
		</div>
	))
  
	useEffect(
	  () => () => {
			files.forEach(file => URL.revokeObjectURL(file.prevew))
	  },
	  [files]
	)

	useEffect(
		() => { 
			props.onLoadFile(files);
		},
		[files]
	)
  
	return (
	  <section className='w-100 position-relative' style={props.style || {}}>
		<div className='h-25 ' {...getRootProps({ className: "dropzone" })}>
		  <input {...getInputProps()} />
		  {
			thumbs.length ? (
			  <div className="thumb-container m-0 ">{thumbs}</div>
			):(
			  <div>
				<div>
				  <UploadCloud className='w-100 d-block text-center' color='white' size={100}/>
				  	<p className='d-blok text-center text-white'>
						{
							props.placeholder ? props.placeholder : "Drag & drop Files here"
						} 
					</p>
				</div>
			  </div>
			)
		  }
		</div>
		{
			thumbs.length > 0 && <div className='text-center'><Button className=	'color-danger' onClick={() => {
				setFiles([])
			}} >Remove</Button></div>
		}
	  </section>
	)
}
  

export default BasicDropzone